import React, {
    useState,
    useCallback,
    useEffect,
    useLayoutEffect
  } from "react";
  import { Pane } from "evergreen-ui";
  import WriteCharactorSearch from "@components/WriteCharactor/WriteCharactorSearch";
import { async } from "q";
import { replace } from "formik";
 
  export default function() {
    const [char, setChar] = useState("");
    const [title, setTitle] = useState("");
    const [content, setContent] = useState([]);
    const [thumbnail, setThumbnail] = useState({} as any);
    const [res ,setRes] = useState({} as any);
    const onSearch = async slug => {
        const fetchingUrl = `https://ytinfo.benjaminlowry.com/${slug}`
        const res = await fetch(fetchingUrl);
        const value = await res.json() as any;
        setChar(slug);
        if(value &&
            value.formats) {
            setRes(value);
            setThumbnail(value.player_response.videoDetails.thumbnail.thumbnails[0]);
            setTitle(value.player_response.videoDetails.title);
            setContent(value.formats.filter(item => item.container == 'mp4') as Array<object>);
        }
    };

    const formatUrl = (url, title)=> {
        url  = url.replace(/\/[^\/]+\.googlevideo\.com/, "/redirector.googlevideo.com")
        .replace('&cms_redirect=yes', '')
        .replace('&redirect_counter=1', '')
        // .replace('&gir=yes', '').replace('gir%2C', '');
        url = url + `&title=${title}`;
        return url;
    }
  
    useEffect(() => {

    }, [char]);

    return (
      <Pane width="800px" minHeight="600px" height="100%" margin="auto">
        <div style={{marginTop: '100px'}}>
            <WriteCharactorSearch
            char={char}
            style={{borderRadius: '50px'}}
            height={60}
            fontSize={'30px'}
            onSearch={onSearch}
            placeholder="Paste the link eg. https://www.youtube.com/watch?v=vl08L2oUvfI"
            ></WriteCharactorSearch>
        </div>
        <div>
        {char && thumbnail.url && <Pane className="media" display="flex" style={{ borderTop: '1px solid #ededed'}} >
            <Pane display="flex">
                <img src={thumbnail.url}  width={thumbnail.width} height={thumbnail.height} alt=""/>
            </Pane>
            <Pane display="flex" flexDirection="column" marginLeft="15px" >
                {char && res.player_response && <h3 style={{textAlign: 'left'}}>{res.player_response.videoDetails.title}</h3>}
                {char && res.player_response && <p style={{color: '#606060'}}>{res.player_response.videoDetails.author}</p>}
            </Pane>
        </Pane>}
        {char && <table className="mimes">
        <tbody>
          <tr>
            <th className="separateColor" style={{ width: "30%" }}>
              Extension
            </th>
            <th>Resolution</th>
            <th>Download</th>
          </tr>
          {content.map((item, ix) => {
            return (
              <tr
                key={ix}
                style={{ cursor: "pointer" }}
              >
                <td>{item.container}</td>
                <td>
                    {item.resolution}
                </td>
                <td><a href={formatUrl(item.url, title)} target="_blank" >download</a></td>
              </tr>
            );
          })}
          {!content.length && (
            <tr>
              <td colSpan={2} style={{ fontSize: "30px", padding: "100px" }}>
                No Results
              </td>
            </tr>
          )}
            </tbody>
        </table>
        }
        </div>
        <style jsx>{`
        .mimes {
          font: 1em Monaco;
          text-align: center;
          border: 1px solid #ccc;
          border-collapse: collapse;
          border-spacing: 0;
          width: 100%;
        }
        .mimes tr > th,
        .mimes tr > td {
          border: 1px solid #ddd;
          padding: 8px;
          line-height: 1.428571429;
          vertical-align: top;
          border-top: 1px solid #ddd;
        }
        .mimes tr > th {
          border-bottom-width: 2px;
          padding: 12px 8px;
        }

        .mimes tr:nth-of-type(odd) {
          background-color: #f9f9f9;
        }

        .mimes tr:hover td {
          background-color: #f5f5f5;
        }
      `}</style>
      </Pane>
    );
  }
  